<template>
    <div class="login-wrap">
      <div class="boxs">
        <div class="boxs-left">
          <img src="@/assets/login.jpg">
        </div>
      <el-form class="login-container">
        <strong class="title">注册界面</strong>
        <el-button type="text" class="regist" @click="route_r()">返回登录</el-button>
        <el-form-item label="名称">
          <el-input type="text" style="width:80%" v-model="user.name" placeholder="输入名称" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" >
          <el-input type="text" style="width:80%" v-model="user.email" placeholder="输入邮箱账号" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="用户密码">
          <el-input type="password" class="smaller" v-model="user.password" placeholder="输入密码" autocomplete="off"></el-input>
        </el-form-item>   
        <el-form-item label="确认密码" >
          <el-input type="password" class="smaller" v-model="c_password" placeholder="重复密码" autocomplete="off" @input="issamepassword"></el-input>
        </el-form-item>  
        <el-form-item>
          <el-button type="primary" style="width: 48%;" @click="doregister()">确认注册</el-button>
          <span style="margin-left: 10%;color:red">{{messagebox}}</span>
        </el-form-item>
      </el-form>

    </div>
    </div>
  </template>
   
  <script>
// import qs from 'qs'
export default {
  name: 'login',
  data: function() {
    return {
        user:{
            name:"",
          email: "",
        password: ""
        },
        c_password:"",
        messagebox:""
    }
  },
  methods: {
    issamepassword(){
      if(this.user.password===this.c_password)
        this.messagebox="" 
      else
        this.messagebox="两次输入密码不一致"
    },
    route_r(){
        this.$router.push({ name: 'login'})
      },
      doregister(){
        if(this.user.password===this.c_password)
          this.canregister()
        else
          this.messagebox="两次输入密码不一致"
      },
    canregister(){
          this.$axios.post("/shui_management/servlet/register",this.user).then((res) => {
        if (res.data.status=="success") {
            this.$message(
                {
                    type:'success',
                    message:'注册成功'
                }
            )
            this.$router.push({ name: 'login'})
             this.messagebox=""
          //登录令牌
            }
            else{
                this.$message(
                {
                    type:"error",
                    message:'注册失败'
                }
            )
                if (res.data.message){
                    this.messagebox=res.data.message
                }
                else {
                    this.messagebox="失败"
                }
            }
        // } else if(res.message=="Email already registered"){
        //    this.messagebox="该邮箱已被注册"

        // }else if(res.message=="2"){
        //     this.messagebox="邮箱格式不正确"
        // }else{
        //     this.messagebox="错误",
        //     this.$message(
        //         {
        //             type:"error",
        //             message:'注册成功'
        //         }
        //     )
        // }

      })
    //   .catch(
    //       this.$message.error("连接服务器失败"),
    //     );
    },
  
  }

    }
  </script>
   
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  .smaller{
    width:72%;
  }
    .login-wrap {
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      padding-top: 10%;
      background-image: url("@/assets/back.jpg");
      /* background-color: #112346; */
      background-repeat: no-repeat;
      background-position: center right;
      background-size: 100%;
    }
    .boxs{
      box-sizing: border-box;
      display: table;
      table-layout: fixed;
      border-radius: 10px;
      margin: 0px auto;
      width:700px;
      padding: 0px 0px 0px 0px;
      background: #fff;
      /* border: 1px solid #eaeaea; */
      text-align: left;
      box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.1);
    }
   .boxs-left{
    display: table-cell;

  width: 260px;
  vertical-align: middle;
  position: relative;
   }
   .boxs img{
    width:100%;
    height:100%;
    border-radius: 10px;
   }
    .login-container {
      position: relative;
  display: table-cell;
  vertical-align: middle;
  border-color: #ddd transparent;
  /* border-style: solid;
  border-width: 1px 0; */
  padding: 0 40px 0 40px;
      /* border-radius: 10px;
      margin: 0px auto;
      width: 350px;
      padding: 30px 35px 15px 35px;
      background: #fff;
      border: 1px solid #eaeaea;
      text-align: left;
      box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.1); */
    }
   
    .title {
      margin: 0px auto 40px auto;
      text-align: left;
      color: #505458;
      font-size: 25px;
    }
    .regist{
        margin-left: 180px;
        text-align: right;
        color: #3a5dbc;
        text-decoration: underline;
        font-size: 15px;
    }
  </style>