<template>
  <div style="background-color: #f5f5f5; height: 750px">
   
    <el-row>
      <el-col :span="14">
        <div class="box ml-30">
          <div style="width: 800px; height: 450px;" ref="lchart"></div>
        </div>
      </el-col>
      <el-col :span="7">
        <div
          class="box ml-30"
          style="width: 500px; height: 450px"
          ref="chart"
        ></div>
      </el-col>
    </el-row>

    
  </div>
</template>

<script>
export default {
  data() {
    return {
      bh:3,
      pe:4,
      rc:5,
      wq:9,
      }
    },
  mounted() {
    new Promise(resolve => {
        this.fetchdata(resolve);
      }).then(res => {
        console.log(res);//数据返回了
      })
  },
  methods: {
   
   fetchdata(resolve){

    this.$axios.get("/shui_management/servlet/getnumber").then((res) => {
      if (res){ this.bh = res.data.bh;
          this.pe = res.data.pe;
          this.rc = res.data.rc;
          this.wq = res.data.wq;
          this.initCharts();
           this.initbar();
          resolve("数据返回了")
        }
      })
    },
    initbar() {
      let mybar = this.$echarts.init(this.$refs.lchart);
      mybar.setOption({
        title: {
          text: "测站各类数量统计",
          left: "center",
        },
        xAxis: {
          data: ["基础水文站", "降水蒸发站", "实时雨水情站", "水质站"],
        },
        yAxis: {},
        series: [
          {
            type: "bar",
            data: [this.bh, this.pe, this.rc,this.wq],
          },
        ],
      });
    },
    initCharts() {
      let myChart = this.$echarts.init(this.$refs.chart);
      myChart.setOption({
        title: {
          text: "测站类别占比图",
          left: "center",
        },
        tooltip: {
          trigger: "item",
        },
        legend: {
          orient: "vertical",
          left: "left",
        },
        series: [
          {
            name: "测站类别",
            type: "pie",
            radius: "60%",
            data: [
              {
                value: this.bh,
                name: "基础水文站",
                itemStyle: {
                  color: "#ee6666",
                },
              },
              {
                value: this.pe,
                name: "降水蒸发站",
                itemStyle: {
                  color: "#1e9eb3",
                },
              },
              {
                value: this.rc,
                name: "实时雨水情站",
                itemStyle: {
                  color: "#f0d695",
                },
              },
              {
                value: this.wq,
                name: "水质站",
                itemStyle: {
                  color: "#91cc75",
                },
              },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      });
    },
  },
};
</script>

<style scoped>
.block {
  margin: 10px;
  position: relative;
  padding-left: 30px; /* 竖线宽度（10px）+ 竖线右侧距离（50px）*/
  line-height: 3; /* 行高，根据需要调整 */
  font-weight: bold;
}

.block::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 5px; /* 竖线宽度，根据需要调整 */
  height: 35px;
  background-color: hsla(230, 47%, 48%, 0.728); /* 竖线颜色，根据需要调整 */
}
.box {
  margin-top: 30px;
  background-color: #ffffff;
  border-radius: 5px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
  padding: 10px;
  text-align: center;
}
.right {
  margin-top: 30px;
  padding-left: 30px;
  text-align: left;
}
.el-progress{
  width:55%;
  height: 100%;
  margin-top:10px ;
  margin-left:30px ;
}
</style>
