import Vue from 'vue';
import VueRouter from 'vue-router';
import Layout from '../views/layout/index-home.vue';
import Home from '../views/home/Home.vue';
import Measurestation from "../views/organization/index.vue";
import Login from "../views/login/login.vue"
import Register from "../views/login/register.vue"




Vue.use(VueRouter);

const routes = [
  {
    path:'/login',
    name:'login',
    component: Login

  },
  {
    path:'/register',
    name:'register',
    component: Register

  },
  {   path: '/', 
      component: Layout,
      meta: {requireAuth:true },
      children:[
        {
          path:'/home',
          name:'home',
          component:Home,
          meta: { title: '测站数据分析',requireAuth:true }
          
        },

        {
          path:'/measure_station',
          name:'measurestation',
          component:Measurestation,
          meta: { title: '测站信息管理',requireAuth:true },
         
        }
      ],
  },
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
  // base: "/shui_management/", //项目名称
  // mode: "history", //消去#
  // routes
})

export default router
export function isUserLoggedIn() {
  return !!localStorage.getItem('token')
}
