          <el-form-item label="邮箱">
            <el-input type="text" v-model="user.email" placeholder="输入邮箱账号" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="密码">
            <el-input type="password" v-model="user.password" placeholder="输入密码" autocomplete="off"></el-input>
          </el-form-item>   
          <el-form-item>
            <el-button type="primary" style="width: 48%;" @click="dologin()">管理员登录</el-button>
