<template>
  <el-container style="height: 700px; border: 1px solid #eee">
    <el-header
      style="padding: 10px; margin: 10px; font-weight: bold; color: #333"
    >
      <div class="demo-input-suffix">
        搜索选项：
        <el-select
          v-model="searchtype"
          filterable
          placeholder="测站名称"
          style="width: 150px; margin-right: 20px"
        >
          <el-option
            v-for="item in joboptions"
            :key="item.value"
            :label="item.value"
            :value="item.value"
          >
          </el-option>
        </el-select>

        查询：
        <el-input
          style="width: 200px; margin-right: 20px"
          placeholder="请输入所要查询内容"
          prefix-icon="el-icon-search"
          v-model="searchcontent"
        >
        </el-input>

        <el-button type="primary" @click="search()" style="margin-left: 30%"
          >搜索</el-button
        >
        <el-button type="success" @click="reload()" style="margin-left: 10px"
          >重置</el-button
        >
        <el-button type="warning" @click="addone()" style="margin-left: 50px"
          ><i class="el-icon-circle-plus-outline"></i> 添加</el-button
        >
      </div>
    </el-header>

    <el-main
      style="box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1); border-radius: 10px"
    >
      <el-table
        :data="tableData"
        border
        size="mini"
        style="width: 100%"
        ref="multipleTable"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="index" width="30"></el-table-column>
        <el-table-column type="selection" width="45"> </el-table-column>
        <el-table-column
          prop="station_name"
          label="测站名称"
          width="120"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="station_id"
          label="测站编码"
          width="180"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="date"
          sortable
          label="建站年月"
          width="100"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="east_longitude"
          label="东经"
          width="100"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="northen_latitude"
          label="北纬"
          width="100"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="address"
          label="站址"
          width="200"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="river_name"
          label="河流名称"
          width="100"
          align="center"
        ></el-table-column>
        <el-table-column
          label="测站类型"
          width="440"
          style="align: middle"
          align="center"
        >
          <el-table-column
            prop="basic_hydrology"
            label="基础水文"
            width="110"
          ></el-table-column>
          <el-table-column
            prop="precipitation_evaporation"
            label="降雨蒸发"
            width="110"
          ></el-table-column>
          <el-table-column
            prop="rain_condition"
            label="实时水雨情"
            width="110"
          ></el-table-column>
          <el-table-column
            prop="water_quality"
            label="水质"
            width="110"
          ></el-table-column>
        </el-table-column>
        <el-table-column
          prop="management"
          label="管理机构"
          width="200"
          align="center"
        ></el-table-column>
        <el-table-column width="150" fixed="right" label="操作" align="center">
          <template slot-scope="scope">
            <!-- 处理按钮事件 -->
            <el-button size="mini" @click="handleEdit(scope.row)"
              >编辑</el-button
            >
              <el-button
                size="mini"
                type="danger"
                @click="open(scope.row.station_id)"
                >删除</el-button
              >
        
          </template>
        </el-table-column>
      </el-table>

      <el-dialog
        title="测站信息"
        :visible.sync="dialogFormVisible"
        append-to-body
      >
        <el-form :model="addform">
          <el-form-item label="测站名称" :label-width="formLabelWidth">
            <el-input
              v-model="addform.station_name"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
            测站编码
            <el-input
              v-model="addform.station_id"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item>
          <el-form-item label="建站年月" :label-width="formLabelWidth">
            <el-date-picker
              v-model="addform.date"
              type="month"
              placeholder="选择年月"
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item label="所在省" :label-width="formLabelWidth">
            <el-input
              v-model="addform.province"
              autocomplete="off"
              style="width: 100px"
            ></el-input>
            所在市
            <el-input
              v-model="addform.city"
              autocomplete="off"
              style="width: 100px"
            ></el-input>
            所在区
            <el-input
              v-model="addform.town"
              autocomplete="off"
              style="width: 100px"
            >
            </el-input>
          </el-form-item>
          <el-form-item label="所在镇" :label-width="formLabelWidth">
            <el-input
              v-model="addform.village"
              autocomplete="off"
              style="width: 100px"
            ></el-input>
            具体街道
            <el-input
              v-model="addform.street"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item>
          <el-form-item label="管理部门" :label-width="formLabelWidth">
            <el-input
              v-model="addform.management"
              autocomplete="off"
              style="width: 400px"
            ></el-input>
          </el-form-item>
          <el-form-item label="东经" :label-width="formLabelWidth">
            <el-input
              v-model="addform.east_longitude"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
            北纬
            <el-input
              v-model="addform.northen_latitude"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item>
          <el-form-item label="河流名称" :label-width="formLabelWidth">
            <el-input
              v-model="addform.river_name"
              autocomplete="off"
              style="width: 100px"
            ></el-input>
            水系
            <el-input
              v-model="addform.river_system"
              autocomplete="off"
              style="width: 100px"
            ></el-input>
            流域
            <el-input
              v-model="addform.drainage"
              autocomplete="off"
              style="width: 150px"
            ></el-input>
          </el-form-item>
          <el-form-item label="基础水文" :label-width="formLabelWidth">
            <el-input
              v-model="addform.basic_hydrology"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
            实时水雨情
            <el-input
              v-model="addform.rain_condition"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item>
          <el-form-item label="降水蒸发" :label-width="formLabelWidth">
            <el-input
              v-model="addform.precipitation_evaporation"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
            水质
            <el-input
              v-model="addform.water_quality"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <!-- 取消与确定按钮事件 -->
          <el-button @click="dialogFormVisible = false">取 消</el-button>

          <el-button type="primary" @click="saveadd">确 定</el-button>
        </div>
      </el-dialog>

      <!--编辑表格-->
      <el-dialog
        title="测站信息"
        :visible.sync="edialogFormVisible"
        append-to-body
      >
        <el-form :model="editform">
          <el-form-item label="测站名称" :label-width="formLabelWidth">
            <el-input
              v-model="editform.station_name"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
            测站编码
            <el-input
              v-model="editform.station_id"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item>
          <el-form-item label="建站年月" :label-width="formLabelWidth">
            <el-date-picker
              v-model="editform.date"
              type="month"
              placeholder="选择年月"
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item label="所在省" :label-width="formLabelWidth">
            <el-input
              v-model="editform.province"
              autocomplete="off"
              style="width: 100px"
            ></el-input>
            所在市
            <el-input
              v-model="editform.city"
              autocomplete="off"
              style="width: 100px"
            ></el-input>
            所在区
            <el-input
              v-model="editform.town"
              autocomplete="off"
              style="width: 100px"
            >
            </el-input>
          </el-form-item>
          <el-form-item label="所在镇" :label-width="formLabelWidth">
            <el-input
              v-model="editform.village"
              autocomplete="off"
              style="width: 100px"
            ></el-input>
            具体街道
            <el-input
              v-model="editform.street"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item>
          <el-form-item label="管理部门" :label-width="formLabelWidth">
            <el-input
              v-model="editform.management"
              autocomplete="off"
              style="width: 400px"
            ></el-input>
          </el-form-item>
          <el-form-item label="东经" :label-width="formLabelWidth">
            <el-input
              v-model="editform.east_longitude"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
            北纬
            <el-input
              v-model="editform.northen_latitude"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item>
          <el-form-item label="河流名称" :label-width="formLabelWidth">
            <el-input
              v-model="editform.river_name"
              autocomplete="off"
              style="width: 100px"
            ></el-input>
            水系
            <el-input
              v-model="editform.river_system"
              autocomplete="off"
              style="width: 100px"
            ></el-input>
            流域
            <el-input
              v-model="editform.drainage"
              autocomplete="off"
              style="width: 150px"
            ></el-input>
          </el-form-item>
          <el-form-item label="基础水文" :label-width="formLabelWidth">
            <el-input
              v-model="editform.basic_hydrology"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
            实时水雨情
            <el-input
              v-model="editform.rain_condition"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item>
          <el-form-item label="降水蒸发" :label-width="formLabelWidth">
            <el-input
              v-model="editform.precipitation_evaporation"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
            水质
            <el-input
              v-model="editform.water_quality"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <!-- 取消与确定按钮事件 -->
          <el-button @click="edialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitedit">确 定</el-button>
        </div>
      </el-dialog>
    </el-main>

    <el-footer>
      <div style="padding: 10px 0">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage3"
          :page-size="10"
          layout="prev, pager, next, jumper"
          :total="100"
        >
        </el-pagination>
      </div>
    </el-footer>
  </el-container>
</template>

<script>
export default {
  inject: ["reload"],
  components: {},
  data() {
    return {
      //搜索框
      dialogFormVisible: false, //添加更新表单
      edialogFormVisible: false,
      searchtype: "测站名称",
      searchcontent: "",

      storeoptions: [],
      addform: {},

      editform: {},

      input1: "",
      border: true,
      // 搜索选择框选项
      joboptions: [
        {
          value: "测站名称",
        },
        {
          value: "测站编码",
        },
        {
          value: "河流名称",
        },
        {
          value: "建站年份",
        },
      ],

      //表格数据
      tableData: [
        {
          basic_hydrology:125,
          
        }
      ],
      multipleSelection: [],

      formLabelWidth: "120px",
    };
  },

  created() {
    this.formload();
  },
  methods: {
    open(id){
      this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.handleDelete(id);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    addone() {
      this.dialogFormVisible = true;
      this.addform = {};
    },
    /////搜索接口设置
    //////////////////////////

    //name
    selectName() {
      const data = { content: this.searchcontent };
      this.$axios
        .post("/shui_management/servlet/selectName", data)
        .then((res) => {
          if (res) {
            this.tableData = res.data;
          } else {
            this.tableData = [];
            this.$message({
              type: "info",
              message: "查无数据",
            });
          }
        });
    },

    selectAddress() {
      const data = { content: this.searchcontent };
      this.$axios
        .post("/shui_management/servlet/selectAddress", data)
        .then((res) => {
          if (res) {
            this.tableData = res.data;
          } else {
            this.tableData = [];
            this.$message({
              type: "info",
              message: "查无数据",
            });
          }
        });
    },

    selectRiver() {
      const data = { content: this.searchcontent };
      this.$axios
        .post("/shui_management/servlet/selectRiver", data)
        .then((res) => {
          if (res) {
            this.tableData = res.data;
          } else {
            this.tableData = [];
            this.$message({
              type: "info",
              message: "查无数据",
            });
          }
        });
    },

    selectId() {
      const data = { content: this.searchcontent };
      this.$axios
        .post("/shui_management/servlet/selectId", data)
        .then((res) => {
          if (res) {
            this.tableData = res.data;
          } else {
            this.tableData = [];
            this.$message({
              type: "info",
              message: "查无数据",
            });
          }
        });
    },
    selectYear() {
      const data = { content: this.searchcontent };
      this.$axios
        .post("/shui_management/servlet/selectYear", data)
        .then((res) => {
          if (res) {
            this.tableData = res.data;
          } else {
            this.tableData = [];
            this.$message({
              type: "info",
              message: "查无数据",
            });
          }
        });
    },
    // //多选处理
    // toggleSelection(rows) {
    //     if (rows) {
    //       rows.forEach(row => {
    //         this.$refs.multipleTable.toggleRowSelection(row);
    //       });
    //     } else {
    //       this.$refs.multipleTable.clearSelection();
    //     }
    //   },
    //   handleSelectionChange(val) {
    //     this.multipleSelection = val;
    //   },

    // getstoreoptions() {
    //   this.$axios.get("/api/store").then((res) => {
    //     this.storeoptions = res.data.map(({ name }) => ({ s_name: name }));
    //   });
    // },
    formload() {
      this.$axios.get("/shui_management/servlet/select_all").then((res) => {
        this.tableData = res.data;
      });
    },

    search() {
      if (this.searchtype === "所在地址") {
        this.selectAddress();
      } else if (this.searchtype === "河流名称") {
        this.selectRiver();
      } else if (this.searchtype === "测站编码") {
        this.selectId();
      } else if (this.searchtype === "建站年份") {
        this.selectYear();
      } else {
        this.selectName();
      }
    },

    load() {},
    //编辑
    submitedit() {
      if(this.editform.station_name!=""&&this.editform.station_id!=""&&this.editform.date!=""){
      this.edialogFormVisible = false;
      this.$axios
        .post("/shui_management/servlet/update", this.editform)
        .then((res) => {
          console.log(res);
          this.$message.success("保存成功");
          this.reload();
        })
        .catch((error) => {
          console.log(error);
          this.$message({
            message: "保存失败",
            type: "warning",
          });
          this.reload();
        });
    }
    else if(this.editform.station_name==""||this.editform.station_id==""){
      this.$message({
            message: "测站名称或编码不能为空",
            type: "warning",
          });
    }
    else if(this.editform.date==""){
      this.$message({
            message: "建站年月不能为空",
            type: "warning",
          });
    }
    else{
      this.$message({
            message: "内容不规范",
            type: "warning",
          });
    }
  },
    //保存信息
    saveadd() {
      this.dialogFormVisible = false;
      this.$axios
        .post("/shui_management/servlet/save", this.addform)
        .then((res) => {
          console.log(res);
          this.$message.success("保存成功");
          this.reload();
        })
        .catch((error) => {
          console.log(error);
          this.$message({
            message: "保存失败",
            type: "warning",
          });
          this.reload();
        });
    },

    //删除
    handleDelete(station_id) {
      const data = { station_id: station_id };
      this.$axios
        .post("/shui_management/servlet/delete", data)
        .then((res) => {
          console.log(res);
          this.$message.success("删除成功");
          this.reload();
        })
        .catch((error) => {
          console.log(error);
          this.$message({
            message: "删除失败",
            type: "warning",
          });
        });
    },

    handleEdit(row) {
      this.editform = row;
      this.edialogFormVisible = true;
    },
  },
};
</script>

<style></style>
